<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process259"></a>

<p><br></p>
<p><span class="rvts17">死刑（</span><span class="rvts50">The Execution</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:318,Y:273</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：玛丽（</span><span class="rvts20">Mary</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">在黛萨彼（</span><span class="rvts20">DeSelby</span><span class="rvts21">）被执行死刑之前，说服凯姆刀下留人</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1427.jpg" display="block" max-width="100%"><span class="rvts20"></span>
<br><span class="rvts21">在到达阿克斯兵营门口处，会自动触发一段刑场剧情。玛丽恳求我们说服凯姆刀下留人，放过她的丈夫戴萨彼。该剧情由于有时间限制，因此在与玛丽对话结束后，应该迅速与旁边的凯姆谈话，不然的话，戴萨彼就会被杀掉。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1428.jpg" display="block" max-width="100%"><span class="rvts20"></span>
<br><span class="rvts21">说服凯姆会有两个桥段。第一个桥段几乎都会是失败的。第二个桥段需求高于</span><span class="rvts20">5</span><span class="rvts21">点的劝说值和额外的智慧（</span><span class="rvts20">Intelligence</span><span class="rvts21">）或智力值（</span><span class="rvts20">Wits</span><span class="rvts21">）。</span>
<br><span class="rvts21">通过第二个桥段后，凯姆会把戴萨彼押进兵营的地下监狱，该支线将以成功方式关闭，别忘了找玛丽领赏。如果第二个桥段未成功通过，那么戴萨彼会被杀掉。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1429.jpg" display="block" max-width="100%"><span class="rvts20"></span>
<br><span class="rvts21">成功拯救戴萨彼后，他会出现在兵营的监狱中，与他谈话，他会告诉你关于凯姆藏宝室的一些异常情况。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



